@tailwind base;
@tailwind components;
@tailwind utilities;


.inputbox{
  background-image: linear-gradient(white, rgb(117, 56, 1), rgb(114, 54, 0), rgb(255, 255, 255),white);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 130px;

}
.clock{

  border: 4px solid #38393a;
  background-color: rgba(79, 46, 18, 0.89);
  border-radius: 10px;
  width: 400px;
}




.intro{
  color: rgba(93, 53, 1, 0.86);
  margin-bottom: 100px;
  font-size: xxx-large;
  font-family: "Arial Rounded MT Bold";

}
.formcontainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 5px solid rgba(137, 143, 60, 0.88);
  height: 100vh;





}
.form{
display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background: rgba(140, 115, 89, 0.89);
  width: 300px;
  height: 300px;
  border-radius: 20px;
  border: 1px solid rgba(40, 0, 0, 0.53);
  box-shadow: 1px 2px 3px #484848;
  transition: 0.4s;
}
.form:hover{
  scale: 130%;
  box-shadow: 6px 8px 15px #2d2d2d;
  background: rgba(182, 180, 101, 0.6);
  border: 0px solid rgba(40, 0, 0, 0.53);
}
.input{
  text-align: center;
  border: 0px;
  box-shadow: 1px 1px 6px gray;
  margin-top: 15px;
  height: 30px;
  border-radius: 8px;
}


.btnsubmit{
  color: white;
  background-color: rgba(61, 35, 0, 0.86);
  border-radius: 5px;
  width: 100px;
  height: 30px ;

}.btnsubmit:hover{
  color: white;
  background-color: rgba(93, 53, 1, 0.86);
  border-radius: 5px;
  box-shadow: 1px 1px 5px #3f3f3f;
}
.btnAccount{
  color: white;
  background-color: rgba(61, 35, 0, 0.86);
  border-radius: 5px;
  width: 120px;
  height: 30px ;
  margin-left: 5px;



}.btnAccount:hover{
  color: white;
  background-color: rgba(93, 53, 1, 0.86);
  border-radius: 5px;
  box-shadow: 1px 1px 5px #3f3f3f;
}



